//
// Component: Button
//
// ========================================================================


// Variables
// ========================================================================

@button-small-font-size: @global-small-font-size;
@button-font-size: @global-font-size;
@button-large-font-size: @global-medium-font-size;
@button-small-line-height: 1;
@button-line-height: 1.5;
@button-large-line-height: 1.75;

@button-secondary-color: @ultikro-dark-teal;
@button-secondary-hover-background: @ultikro-light-teal-40;
@button-secondary-hover-color: @ultikro-dark-teal;
@button-secondary-background: @ultikro-light-teal-80;
@button-secondary-active-background: @ultikro-blue-40;
@button-secondary-active-color: @ultikro-dark-teal;

@button-primary-hover-background: @ultikro-dark-teal-80;
@button-primary-active-background: @ultikro-blue-40;
@button-primary-active-color: @ultikro-dark-teal;

@button-default-background: @white;
@button-default-color: @black;


// Component
// ========================================================================

.hook-button() {
  display: inline-flex;
  align-items: center;
  font-family: @volte;
  font-weight: 600;
  text-transform: uppercase;
  transition: .1s ease-in-out;
  transition-property: color, background-color, background-position, border-color, box-shadow;
  border-radius: 500px;
  padding: 0.5rem 1.5rem;
  background-origin: border-box;
  border: 0;
  height: 2.5rem;
  @media (max-width: @breakpoint-small-max) {
    font-size: 0.8rem !important;
    line-height: 1 !important;
    padding: @padding-small-padding !important;
  }
}

.hook-button-small() {
  padding: 0.5rem 1rem;
  height: 2rem;
  font-weight: 500;
}

.hook-button-large() {
  padding: 0.75rem 1.5rem;
  height: 3rem;
}

.hook-button-active() {
  border: 2px solid @ultikro-dark-teal-60;
}

