aside.ukg-sidebar {
  max-width: 400px;
  margin: 0 auto;
  border-radius: @global-container-border-radius;
  padding: @global-small-gutter;

  &.ukg-sidebar-form {
    .ukg-card-form {
      > .uk-card {
        padding: 0;
        .ukg-sidebar-section{
          padding-top: 0;
        }
      }
    }

    .uk-button {
      margin-top: @global-medium-gutter;
    }
    @media (min-width: @breakpoint-medium) {
      padding: @global-gutter;
    }
  }

  &.ukg-sidebar-primary {
    padding-bottom: @global-large-gutter;

    .ukg-sidebar-section {
      border-bottom: 3px solid @white;
      padding: @global-small-gutter @global-small-gutter @global-small-gutter 0;

      &:last-child {
        border-bottom: none;
      }
    }

    .ukg-press-contact {
      margin-bottom: @global-small-gutter;

      h3, h4 {
        margin-bottom: 0;
      }

      .ukg-contact-company {

      }
    }
  }

  .ukg-snippet-share {
    &:extend(.ukg-snippet-follow all);
    text-align: center;
    justify-content: flex-start;
    align-self: inherit;
    top: 0;

    a.icon {
      margin-left: 0;
      margin-right: 1.5rem;

      > span {
        display: none;
      }

      &:before, &:after {
        color: @ultikro-light-teal;
        font-size: @global-large-font-size;
      }
    }
  }

  .ukg-sidebar-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    a + a {
      margin-top: @global-xsmall-gutter;
    }

    img {
      margin-bottom: @global-small-gutter;
    }

  }
}

.ukg-sidebar-primary {
  background-color: @ultikro-dark-teal;
  color: @white;

  h2, h3, h4, p {
    color: @white;
  }

  a {
    color: @ultikro-light-teal;
  }
}
