// Name:            Visibility
// Description:     Utilities to show or hide content on breakpoints, hover or touch
//
// Component:       `uk-hidden-*`
//                  `uk-visible-*`
//                  `uk-invisible`
//                  `uk-visible-toggle`
//                  `uk-hidden-hover`
//                  `uk-invisible-hover`
//                  `uk-hidden-touch`
//                  `uk-hidden-notouch`
//
// ========================================================================


/* ========================================================================
   Component: Visibility
 ========================================================================== */

/*
 * Hidden
 * `hidden` attribute also set here to make it stronger
 */

/*
 * uk-hidden@xs
 * Added uk-hidden@xs for UKG theme
 */

.hook-visibility-misc() {
  /* Phone landscape and bigger */
  @media (min-width: @breakpoint-xsmall) {
    .uk-hidden\@xs {
      display: none !important;
    }
  }
  @media (max-width: @breakpoint-default-max) {
    .uk-visible\@xs {
      display: none !important;
    }
  }

  /* 2k and smaller */
  @media (max-width: @breakpoint-xlarge-max) {
    .uk-visible\@xxl { display: none !important; }
  }

  /* 4k and smaller */
  @media (max-width: @breakpoint-xxlarge-max) {
    .uk-visible\@xxxl { display: none !important; }
  }

}
