//
// Component: Text
//
// ========================================================================


// Variables
// ========================================================================

//
// New
//

@text-meta-link-color: @text-meta-color;
@text-meta-link-hover-color: @global-color;


// Style modifiers
// ========================================================================
.uk-text-white {
  color: @white !important;
}

.uk-text-black {
  color: @black !important;
}

.uk-text-muted {
  color: @ultikro-light-grey !important;
}

.uk-text-emphasis {
  color: @ultikro-blue !important;
}

.uk-text-primary {
  color: @ultikro-dark-teal !important;
}

.uk-text-secondary {
  color: @ultikro-light-teal !important;
}

.uk-text-success {
  color: @ultikro-dark-teal !important;
}

.uk-text-warning {
  color: @ultikro-light-teal !important;
}

.uk-text-danger {
  color: @ultikro-blue !important;
}


/* Background modifier
 ========================================================================== */

/*
 * 1. The background clips to the foreground text. Works in Chrome, Firefox, Safari, Edge and Opera
 *    Default color is set to transparent
 * 2. Container fits the text
 * 3. Fallback color for IE11
 */

.hook-text-lead() {
}

.hook-text-meta() {

  a {
    color: @text-meta-link-color;
  }

  a:hover {
    color: @text-meta-link-hover-color;
    text-decoration: none;
  }

}


// Size modifiers
// ========================================================================

.hook-text-small() {
}

.hook-text-large() {
}


// Background modifier
// ========================================================================

.hook-text-background() {
