//
// Component: Accordion
//
// ========================================================================


// Variables
// ========================================================================
@accordion-title-font-size: 1rem;
@accordion-title-line-height: 1.5;
@accordion-title-font-family: @volte;
//
// New
//

@accordion-icon-margin-left: 10px;
@accordion-icon-color: @ultikro-dark-teal;
@internal-accordion-open-image: "../../images/backgrounds/accordion-open.svg";
@internal-accordion-close-image: "../../images/backgrounds/accordion-close.svg";


// Component
// ========================================================================

.hook-accordion() {
  > li {
    padding: 0;

    &.uk-open {
      .uk-accordion-title {
        &::before {
          content: "\e92f";
        }
      }
    }
  }
  &.ukg-accordion-primary {
    .uk-accordion-title {
      background-color: @ultikro-dark-grey-40;
      color: @ultikro-dark-teal;
    }

    > li.uk-open {
      .uk-accordion-title {
        background-color: @ultikro-light-teal;
      }
    }
  }
  &.ukg-accordion-muted {
    .uk-accordion-title {
      background-color: @ultikro-blue;
      color: @white;
    }

    > li.uk-open {
      .uk-accordion-title {
        background-color: @ultikro-dark-teal;
      }
    }
  }
  &.ukg-accordion-default {
    .uk-accordion-title {
      background-color: @ultikro-dark-grey-40;
      color: @ultikro-dark-teal;
    }

    > li.uk-open {
      .uk-accordion-title {
        background-color: @ultikro-dark-teal;
        color: @white;
      }
    }
  }
}


// Item
// ========================================================================

.hook-accordion-item() {
  margin-top: @global-xsmall-gutter;
}


// Title
// ========================================================================

.hook-accordion-title() {

  overflow: hidden;
  padding: @global-small-gutter;
  font-family: @accordion-title-font-family;
  font-weight: 600;
  text-transform: uppercase;
  display: flex;
  align-items: center;

  &::before {
    font-family: 'icomoon' !important;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    font-size: 0.75rem;
    content: "\e928";
    margin-right: @global-small-gutter;
  }

  &::after {
    content: "";
    display: none;
  }


}

.hook-accordion-title-hover() {
}


// Content
// ========================================================================

.hook-accordion-content() {
  margin: 0;
  padding: @global-gutter @global-small-gutter;
}


// Miscellaneous
// ========================================================================

.hook-accordion-misc() {
}

// Inverse
// ========================================================================

.hook-inverse-accordion-item() {
}

.hook-inverse-accordion-title() {
}

.hook-inverse-accordion-title-hover() {
}

.hook-inverse() {

  .uk-accordion-title::before {
    .svg-fill(@internal-accordion-close-image, "#000", @inverse-global-color);
  }

  .uk-open > .uk-accordion-title::before {
    .svg-fill(@internal-accordion-open-image, "#000", @inverse-global-color);
  }

}
