//
// Component: Link
//
// ========================================================================


// Variables
// ========================================================================
@link-cta-color: @ultikro-dark-teal;
@link-cta-color-secondary: @ultikro-light-teal;
@cta-link-size: @global-font-size;
@cta-link-size-large: @global-medium-font-size;
@cta-link-size-small: @global-small-font-size;
// Muted
// ========================================================================

.hook-link-muted() {
}

.hook-link-muted-hover() {
}


// Text
// ========================================================================

.hook-link-text() {
}

.hook-link-text-hover() {
}


// Heading
// ========================================================================

.hook-link-heading() {
}

.hook-link-heading-hover() {
}


// Reset
// ========================================================================

.hook-link-reset() {
}

// Functions
// ========================================================================

.ukg-link-chevron {
  &:after {
    font-family: 'icomoon' !important;
    font-size: @cta-link-size * 0.75;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    content: "\e930";
    color: inherit;
    margin-left: @cta-link-size * 0.8;
    margin-bottom: 2px;
  }
}

.ukglink {
  position: relative;
  font-size: @cta-link-size;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-family: @volte;
  font-weight: 600;
  box-sizing: border-box;
  height: 2rem;

  &:extend(.ukg-link-chevron all);

  &:hover, &:focus, &:visited {
    border-bottom: 1px solid;
    text-decoration: none;
  }
}

// Miscellaneous
// ========================================================================

.hook-link-misc() {
  &.ukg-cta {
    color: @link-cta-color;
    &:extend(.ukglink all);
  }
  &.ukg-cta-default {
    color: @white !important;
    &:extend(.ukglink all);

    &:hover,
    &:focus {
      color: @white !important;
    }
  }
  &.ukg-cta-primary {
    color: @link-cta-color !important;
    &:extend(.ukglink all);

    &:hover,
    &:focus {
      color: @link-cta-color !important;
    }
  }
  &.ukg-cta-secondary {
    color: @link-cta-color-secondary !important;
    &:extend(.ukglink all);

    &:hover,
    &:focus {
      color: @link-cta-color-secondary !important;
    }
  }
  &.ukg-cta-black {
    color: @black !important;
    &:extend(.ukglink all);

    &:hover,
    &:focus {
      color: @black !important;
    }
  }

  &.ukg-cta-large {
    font-size: @cta-link-size-large;
    height: 2.5rem;
  }
  &.ukg-cta-small {
    font-size: @cta-link-size-small !important;
    height: 1.5rem !important;
  }
  &.ukg-cta-inline {
    font-size: inherit;
    text-transform: none;
    word-break: break-word;

    &:after {
      content: none !important;
    }
  }
  &.ukg-cta-inline-blue {
    &:extend(.ukg-cta-inline all);
    color: @ultikro-blue-link !important;

    &:hover {
      color: @ultikro-dark-teal !important;
    }
  }
}
