//
// Component: Breadcrumb
//
// ========================================================================


// Variables
// ========================================================================
@breadcrumb-item-color: @ultikro-dark-grey-80;
@breadcrumb-divider-color: @ultikro-dark-grey-80;
@inverse-breadcrumb-item-active-color: @ultikro-dark-teal;

// Component
// ========================================================================

.ukg-section-breadcrumb {
  margin: 0;
}

.hook-breadcrumb() {
  > ul {
    padding-left: 0;
    flex: auto;
    display: flex;
    flex-direction: column;
    margin-bottom: 0;
    padding: @global-small-gutter 0;

    > li {
      display: inline-flex;
      align-items: center;

      > a.ukg-breadcrumb-item {
        color: @ultikro-dark-grey-80;
        font-size: @global-font-size;
        font-weight: 600;
        box-sizing: border-box;
        height: 2rem;
        display: flex;
        align-items: center;

        &:hover, &:active, &:focus {
          text-decoration: none;
          border-bottom: 1px solid @ultikro-dark-teal;
          color: @ultikro-dark-teal;
        }
      }

      > span.icon-chevron-right {
        margin: 0 0.5rem;
        font-size: 0.7rem;
        font-weight: 600;
        position: relative;
        display: flex;
        align-items: center;
        top: 1px;
      }

      > span.active {
        color: @ultikro-dark-teal;
        font-size: @global-font-size;
        font-weight: 600;
      }
    }
    @media (min-width: @breakpoint-small) {
      flex-direction: row;
      align-items: baseline;
    }
  }
}


