//
// Component: Pagination
//
// ========================================================================


// Variables
// ========================================================================
@pagination-item-color: @ultikro-dark-teal;

// Component
// ========================================================================

.hook-pagination() {
  display: flex;
  align-items: center;
  > li {
    &.pager__item {
      &.uk-border-circle {
        width: 1rem;
        height: 1rem;
        padding: 0.8rem;
        background-color: @ultikro-light-grey;
        margin-left: 1rem;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        font-weight: 600;

        &:hover, &:active, &:focus {
          background-color: @ultikro-dark-teal-60 !important;

          > a {
            color: @white !important;
          }
        }

        &.uk-active {
          background-color: @ultikro-dark-teal !important;

          > a {
            color: @white;
          }
        }
      }
    }
  }
}


// Items
// ========================================================================

.hook-pagination-item() {
  transition: color 0.1s ease-in-out;
}

.hook-pagination-item-active() {
  > a {
    &.pager__item--next, &.pager__item--previous {
      border-bottom: 1px solid @ultikro-dark-teal;
    }
  }
}

.hook-pagination-item-disabled() {
}


// Miscellaneous
// ========================================================================

.hook-pagination-misc() {
}


// Inverse
// ========================================================================

.hook-inverse-pagination-item() {
}

.hook-inverse-pagination-item-hover() {
}

.hook-inverse-pagination-item-active() {
