//
// Component: List
//
// ========================================================================


// Variables
// ========================================================================

//
// New
//

.ukg-list-transparent, .ukg-slider-transparent {
  background-color: transparent;
}

.ukg-list-primary, .ukg-slider-primary {
  background-color: @ultikro-dark-teal;
}

.ukg-list-secondary, .ukg-slider-secondary {
  background-color: @ultikro-light-teal;
}

.ukg-list-default, .ukg-slider-default {
  background-color: @white;
}

.ukg-list-blue, .ukg-slider-blue {
  background-color: @ultikro-blue;
}

.ukg-list-muted, .ukg-slider-muted {
  background-color: @ultikro-light-grey;
}

.ukg-card-secondary60 .ukg-list-transparent {
  border-color: @ultikro-dark-teal;

  .event-title, p {
    color: @ultikro-dark-teal;
  }
}

.ukg-list-event {
  padding: @global-small-gutter 0;
}

.ukg-events-listing-board {
  .ukg-list-event {
    border-bottom: 2px solid;

    .event-title {
      margin-bottom: @global-xsmall-gutter;
    }

    &:last-child {
      border-bottom: none;
    }

    p {
      margin: 0 0 @global-xsmall-gutter;
    }

    * + p {
      margin-top: @global-xsmall-gutter;
    }

  }
}


.ukg-card-listing {
  > .uk-card {
    max-width: @uk-banner-max-width;
    margin: 0 auto;

    .uk-card-body, .uk-card-header, .uk-card-footer {
      padding: 0;
    }
  }
}

.ukg-list-image {
  .uk-card-media {

  }

  > .uk-card {
    padding: 0;

    > .ukg-list-text {
      padding-left: @global-small-gutter;
    }

    .uk-card-body, .uk-card-footer {
      margin-bottom: @global-small-gutter;
    }

    .uk-card-header {

    }

    .uk-card-body {

    }

    .uk-card-footer {
      margin-top: @global-small-gutter;
    }

  }

  margin-bottom: @global-gutter;
}

.ukg-list-image-wide {
  &:extend(.ukg-list-image all);
  margin-bottom: 0;
  padding: @padding-padding @padding-small-padding;
}

.ukg-list-blog {
  .uk-card-header {
    margin: 0 0 10px 0 !important;
    h3.uk-card-title {
      margin: 0;
    }
  }
  .uk-card-body{
    margin: 10px 0 !important;
  }

  .uk-card-footer {
    margin: 10px 0 !important;
    .meta-container {
      display: flex;
      align-items: center;
      flex-wrap: wrap;

      .blog-date {
        margin-right: @global-small-gutter;
        color: @ultikro-dark-teal;
        font-family: @sans_pro_bold;
      }

      a {
        color: @ultikro-dark-teal;
        margin-right: @global-small-gutter;
        display: inline-flex;
        align-items: center;

        span.icon-tag {
          padding-right: 5px;
        }

        span.tag-name {
          font-family: @sans_pro_bold;
        }

        &:hover {
          text-decoration: none;
          color: @ultikro-light-teal;

          span.icon-tag {
            color: @ultikro-light-teal;
          }
        }
      }


    }
  }
}

.ukg-list-news-item {
  > .uk-card {
    padding: 0;

    .uk-card-body, .uk-card-footer {
      margin-bottom: @global-small-gutter;
    }

    .uk-card-body {
      a {
        &:extend(.ukg-cta-inline-blue all);
      }
    }

    .uk-card-footer {
      margin-top: @global-small-gutter;
    }

    .uk-card-header {
      .ukg-list-prehead {
        text-transform: uppercase;
      }
    }

  }

  margin-bottom: @global-gutter;
}

.ukg-list-event-landing {
  padding: @global-gutter @global-xsmall-gutter @global-xsmall-gutter;
  margin-bottom: 0;
  margin-right: @global-small-gutter;

  > .uk-card {
    padding: 0;

    .uk-card-body, .uk-card-footer {
      margin-bottom: @global-small-gutter;
    }

    .uk-card-footer {
      margin-top: @global-small-gutter;
    }

    .uk-card-header {
      .ukg-list-prehead {
        text-transform: uppercase;
        letter-spacing: 1px;
        margin: 0;
      }
      .uk-card-title {
        font-size: @large-card-heading;
      }        
    }

  }

  &:nth-child(odd) {
    background-color: #eee;
  }


}

.ukg-list-default {
  .uk-card-header {
    .ukg-list-prehead {
      color: @black;
    }

    .uk-card-title {
      color: @ultikro-dark-teal;
    }
  }

  .uk-card-body {
    color: @black;
  }

  .uk-card-footer {
    .meta-tags {
      a {
        color: @black;
        margin-right: 5px;
        display: inline-flex;
        align-items: center;

        &:hover {
          text-decoration: none;
          color: @ultikro-light-teal;

          span.icon-tag {
            color: @ultikro-light-teal;
          }
        }
      }

      span.icon-tag {
        color: @ultikro-dark-teal;
        padding-right: 5px;
      }
    }
  }
}

.ukg-search-listing {
  margin: @global-small-gutter 0;

  .ukg-list-search-item {
    .ukg-search-image {
      display: none;
      @media (min-width: @breakpoint-small) {
        display: inline-flex;
        max-width: 300px;
      }
    }

    .uk-search-body {
      display: flex;
      flex-direction: column;
      max-width: 1000px;

      .meta-container {
        flex-direction: column;
        align-items: flex-start;

        a.ukg-meta-info-cta {
          font-size: 0.9rem;
          text-transform: none;
        }

        .tags, .ukg-news-info {
          display: inline-flex;

          > li, span {
            margin-right: @global-small-gutter;
          }
        }
      }
    }
  }
}
