//
// Component: Container
//
// ========================================================================


// Remove all cohesion coh-container pseudo element styles
// ========================================================================


// Miscellaneous
// ========================================================================

.coh-container, .coh-wysiwyg {
  &:before, &:after {
    content: none;
  }
}

.ukg-slider {
  .uk-slider-items {
    &.uk-flex-middle {
      li.coh-inline-element {
        .coh-slider-item {
          > .coh-container {
            margin-bottom: 0;
          }
        }

        .coh-image {
          align-self: center;
        }
      }
    }
  }
}

.ukg-card-bio > .uk-card {
  .coh-image {
    display: initial;
  }
}

.coh-accordion-tabs-content {
  .ukg-card-base, .ukg-card-image, .ukg-card-quote, .ukg-card-text {
    margin-bottom: @global-small-gutter;
  }
}
