// Panel
// ========================================================================

.hook-panel-scrollable() {
}

.uk-panel {
  margin: @global-small-gutter auto !important;

  &.uk-panel-small {
    max-width: 680px;
  }

  &.uk-panel-medium {
    max-width: 800px;
  }

  &.uk-panel-large {
    max-width: 1000px;
  }

  &.ukg-contact-banner {
    h2, h3, a.uk-button {
      margin: @global-small-gutter auto;
    }

    h3 {
      font-size: 1.3rem;
    }
  }
}

/* Border
 ========================================================================== */

.uk-border-circle {
  border-radius: 50%;
}

.uk-border-pill {
  border-radius: 12px;

  img, iframe {
    border-radius: 12px;
  }
}

.uk-border-rounded {
  border-radius: @border-rounded-border-radius;
}


