.ukg-main-content {
  font-size: 1rem;
  color: @black;

  p {
    text-align: left;
    color: @black;
  }

  a:not([class]) {
    color: @ultikro-blue-link !important;

    &:hover {
      color: @ultikro-dark-teal !important;
    }
  }

  .uk-section {
    padding: 0;
    margin-top: @global-small-gutter;
  }
}

.ukg-subtitle {
  margin-top: @global-small-gutter;
}

.ukg-content-body {
  margin: @global-small-gutter auto;

  b, strong {
    font-family: @sans_pro_bold;
  }

  i {
    font-family: @sans_pro_light_It;
  }
}

