.hook-list-misc() {
  // added counter support for automatic numbering
  .uk-list-lower-roman > ::before {
    content: counter(rules, lower-roman);
    left: -50px;
  }
  .uk-list-lower-roman > * {
    counter-increment: rules;
  }
  .uk-list-lower-roman {
    padding-left: @list-padding-left;
    counter-reset: rules;

    li {
      padding-left: 20px;
    }
  }
  .uk-list-bullet > ::before {
    background-image: url("../files/images/background/list-bullet.svg");
  }
}
