//
// Component: Animation
//
// ========================================================================


// Variables
// ========================================================================


// Miscellaneous
// ========================================================================

.hook-animation-misc() {}


@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-moz-keyframes fade-in { /* Firefox */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes fade-in { /* Safari and Chrome */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-o-keyframes fade-in { /* Opera */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@-moz-keyframes fade-out { /* Firefox */
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@-webkit-keyframes fade-out { /* Safari and Chrome */
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@-o-keyframes fade-out { /* Opera */
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.fadeOut {
  -moz-animation: fade-out 1s ease-in forwards; /* Firefox */
  -webkit-animation: fade-out 1s ease-in forwards; /* Safari and Chrome */
  -o-animation: fade-out 1s ease-in forwards; /* Opera */
  animation: fade-out 1s ease-in forwards;
}

.fadeOutFast {
  -moz-animation: fade-out 0.5s ease-in forwards; /* Firefox */
  -webkit-animation: fade-out 0.5s ease-in forwards; /* Safari and Chrome */
  -o-animation: fade-out 0.5s ease-in forwards; /* Opera */
  animation: fade-out 0.5s ease-in forwards;
}

.fadeIn {
  -moz-animation: fade-in 0.5s linear forwards; /* Firefox */
  -webkit-animation: fade-in 0.5s linear forwards; /* Safari and Chrome */
  -o-animation: fade-in 0.5s linear forwards; /* Opera */
  animation: fade-in 0.5s linear forwards;
}

.fadeInFast {
  -moz-animation: fade-in 0.1s linear forwards; /* Firefox */
  -webkit-animation: fade-in 0.1s linear forwards; /* Safari and Chrome */
  -o-animation: fade-in 0.1s linear forwards; /* Opera */
  animation: fade-in 0.1s linear forwards;
}
