//
// Component: Heading
//
// ========================================================================


// Variables
// ========================================================================

@heading-small-font-size:                       @heading-small-font-size-m * 0.8;      // 38px 0.73
@heading-small-font-size-m:                     @heading-small-font-size-l * 0.8125;   // 3.25rem / 52px
@heading-small-font-size-l:                     2.5rem;                                  // 64px // 176px
@heading-small-line-height:                     1.1;


// Component
// ========================================================================

.hook-heading-small() {
  font-size: @heading-small-font-size;
  line-height: @heading-small-line-height;
}


/* Tablet Landscape and bigger */
@media (min-width: @breakpoint-medium) {
  .uk-heading-small { font-size: @heading-small-font-size-m !important; }
}

/* Laptop and bigger */
@media (min-width: @breakpoint-large) {
  .uk-heading-small { font-size: @heading-small-font-size-l !important; }
}

