//
// Component: Padding
//
// ========================================================================


// Variables
// ========================================================================


// Miscellaneous
// ========================================================================
.hook-padding-misc() {
  /* Desktop and bigger */

  .uk-padding-left {
    padding-left: @global-gutter !important;
  }
  .uk-padding-left-small {
    padding-left: @global-small-gutter !important;
  }
  .uk-padding-left-large {
    padding-left: @global-gutter !important;
  }
  .uk-padding-right {
    padding-right: @global-gutter !important;
  }
  .uk-padding-right-small {
    padding-right: @global-small-gutter !important;
  }
  .uk-padding-right-large {
    padding-right: @global-gutter !important;
  }
  .uk-padding-top {
    padding-top: @global-gutter !important;
  }
  .uk-padding-top-small {
    padding-top: @global-small-gutter !important;
  }
  .uk-padding-top-large {
    padding-top: @global-gutter !important;
  }
  .uk-padding-bottom {
    padding-bottom: @global-gutter !important;
  }
  .uk-padding-bottom-small {
    padding-bottom: @global-small-gutter !important;
  }
  .uk-padding-bottom-large {
    padding-bottom: @global-gutter !important;
  }
  @media (min-width: @breakpoint-medium) {
    uk-padding\@m{
      padding: @global-gutter !important;
    }
    uk-padding-remove-top\@m {
      padding-top: 0 !important;
    }
    .uk-padding-right-large\@m {
      padding-right: @global-gutter !important;
    }
  }
  @media (min-width: @breakpoint-large) {

    .uk-padding-left {
      padding-left: @global-large-gutter !important;
    }

    .uk-padding-left-small {
      padding-left: @global-gutter !important;
    }

    .uk-padding-left-large {
      padding-left: @global-large-gutter !important;
    }

    .uk-padding-right {
      padding-right: @global-large-gutter !important;
    }

    .uk-padding-right-small {
      padding-right: @global-gutter !important;
    }

    .uk-padding-right-large {
      padding-right: @global-large-gutter !important;
    }

    .uk-padding-right-large\@l {
      padding-right: @global-large-gutter !important;
    }

    .uk-padding-top {
      padding-top: @global-large-gutter !important;
    }

    .uk-padding-top-small {
      padding-top: @global-gutter !important;
    }

    .uk-padding-top-large {
      padding-top: @global-large-gutter !important;
    }

    .uk-padding-bottom {
      padding-bottom: @global-large-gutter !important;
    }

    .uk-padding-bottom-small {
      padding-bottom: @global-gutter !important;
    }

    .uk-padding-bottom-large {
      padding-bottom: @global-large-gutter !important;
    }
  }
}
