// Name:            Slider
// Description:     Component to create horizontal sliders
//
// Component:       `uk-slider`
//
// Sub-objects:     `uk-slider-container`
//                  `uk-slider-items`
//
// States:          `uk-active`
//
// ========================================================================


/* ========================================================================
   Component: Slider
 ========================================================================== */

// Hooks
// ========================================================================

.hook-slider-misc;

.hook-slider() {
  padding: @global-small-gutter;
  border-radius: @global-container-border-radius;
  .uk-slider-container {
    border-radius: 12px;
  }
  &.ukg-slider-primary {
    .uk-slider-container {
      border: 1px solid @ultikro-dark-teal;
    }
  }
  &.ukg-slider-muted {
    .uk-slider-container {
      border: 1px solid @ultikro-light-grey;
    }
  }
  &.ukg-slider-nav-outside {
    padding-left: @global-medium-gutter;
    padding-right: @global-medium-gutter;

    .uk-slidenav-previous {
      margin-right: 0;
    }

    .uk-slidenav-next {
      margin-left: 0;
    }

    @media (min-width: @breakpoint-medium) {

      .uk-slidenav-previous {
        margin-right: 0;
      }

      .uk-slidenav-next {
        margin-left: 0;
      }

    }
    @media (min-width: @breakpoint-large) {
      padding-left: @global-large-gutter;
      padding-right: @global-large-gutter;
    }
  }
}

.hook-slider-misc() {
}
