//
// Component: Card
//
// ========================================================================
@card-transparent-background: transparent;
@card-transparent-color: @white;
@card-transparent-title-color: @white;
@card-transparent-hover-background: transparent;

@card-body-padding-horizontal: 0;
@card-body-padding-vertical: 0;
@card-header-padding-vertical: 0;
@card-header-padding-horizontal: 0;
@card-footer-padding-horizontal: 0;
@card-footer-padding-vertical: 0;

// Miscellaneous
// ========================================================================
@uk-card-max-width: @global-card-container-max-width;
@uk-banner-max-width: @global-text-container-max-width;
@hero-card-max-width-mobile: @uk-card-max-width;
@quote-banner-max-width: @uk-banner-max-width;
@quote-card-max-width: @uk-card-max-width;
@large-card-heading: 2rem;
@quote-padding: 50px;
@border-radius: @global-container-border-radius;
@internal-form-required-icon: "../files/images/background/required.svg";

.hook-card {
  padding: @global-small-gutter;
  border-radius: @border-radius;
}

.ukg-card-base,
.ukg-card-carousel,
.ukg-card-icon,
.ukg-card-bio,
.ukg-card-customer,
.ukg-card-image,
.ukg-card-background-image,
.ukg-banner-quote,
.ukg-callout-menu-content,
.ukg-card-quote {
  > .uk-card {

    .uk-card-body {
      padding: @global-small-gutter 0;
    }

    .uk-card-header {
      padding: 0;
    }

    .uk-card-footer {
      display: flow-root;
      padding: 0;
    }

    /* Desktop and bigger */
    @media (min-width: @breakpoint-large) {

      .uk-card-body {
        padding: @global-small-gutter 0;
      }

      .uk-card-header {
        padding: 0;
      }

      .uk-card-footer {
        padding: 0;
      }

    }

  }
}


.ukg-card-primary, .ukg-callout-primary {
  > .uk-card {
    background-color: @ultikro-dark-teal;

    .ukg-card-icon-icon {
      background-color: @white;
    }

    .uk-card-header {
      color: @white;
    }

    .uk-card-body {
      color: @white;
    }

    .uk-card-title {
      color: @white;
    }
  }
}


.ukg-card-secondary, .ukg-callout-secondary {
  > .uk-card {
    background-color: @ultikro-light-teal;

    .ukg-card-icon-icon {
      background-color: @white;
    }

    .uk-card-header {
      color: @ultikro-dark-teal;
    }

    .uk-card-title {
      color: @ultikro-dark-teal;
    }

    .uk-card-body {
      color: @ultikro-dark-teal;
    }
  }
}

.ukg-card-secondary60 {
  &:extend(.ukg-card-secondary all);

  > .uk-card {
    background-color: @ultikro-light-teal-60;
  }
}

.ukg-card-secondary40 {
  &:extend(.ukg-card-secondary all);

  > .uk-card {
    background-color: @ultikro-light-teal-40;
  }
}


.ukg-card-iframe {
  &:extend(.ukg-card-image all);

  .uk-card-media-top {
    min-height: 150px;
    border-radius: @border-radius @border-radius 0 0;
  }

  .coh-container-boxed {
    margin: 0 auto !important;
  }
}

.ukg-card-muted, .ukg-callout-muted {
  > .uk-card {
    background-color: @ultikro-light-grey;

    .uk-card-header {
      color: @ultikro-dark-teal;
    }

    .uk-card-title {
      color: @ultikro-dark-teal;
    }

    .uk-card-body {
      color: @ultikro-dark-teal;
    }
  }
}

.ukg-card-default, .ukg-callout-default {
  > .uk-card {
    background-color: @white;

    .ukg-card-icon-icon {
      background-color: @ultikro-dark-teal;
    }

    .uk-card-header {
      color: @ultikro-dark-teal;
    }

    .uk-card-title {
      color: @ultikro-dark-teal;
    }

    .uk-card-body {
      color: @ultikro-dark-teal;
    }
  }
}

.ukg-card-blue, .ukg-callout-blue {
  > .uk-card {
    background-color: @ultikro-blue;

    .uk-card-header {
      color: @black;
    }

    .ukg-card-icon-icon {
      background-color: @white;
    }

    // This is inconsistant, we leave it to the components
    .uk-card-title {
      color: @black;
    }

    .uk-card-body {
      color: @black;
    }
  }
}

.ukg-card-blue40 {
  &:extend(.ukg-card-blue all);

  > .uk-card {
    background-color: @ultikro-blue-40;
  }
}


/*
 * Default
 * Note: Header and Footer are only implemented for the default style
 */

.ukg-card, .ukg-card-icon, .ukg-card-base, .ukg-card-carousel {
  > .uk-card {
    padding: @global-small-gutter;
    border-radius: 12px;
    @media (min-width: @breakpoint-medium) {
      padding: @global-gutter;
    }
  }
}

.ukg-card-transparent {
  background: @card-transparent-background;
  color: @card-transparent-color;

  &.ukg-card-hero-quote {
    > .ukg-card-text {
      h1, h2, h3, p {
        color: @card-transparent-color;
      }
    }
  }
}


.ukg-card-transparent .uk-card-title {
  color: @card-transparent-title-color;
}

.ukg-card-transparent.uk-card-hover:hover {
  background-color: @card-transparent-background;
}

.ukg-card-transparent .uk-card-header {
}

.ukg-card-text, .ukg-card-media, .ukg-card-icon {
  display: flex;
  align-items: center;
}

.ukg-card-text {
  > .uk-card {
    .uk-card-footer, .uk-card-body, .uk-card-header {
      padding-left: 0;
    }
  }
}

.ukg-card-text {
  text-align: center;
  @media (min-width: @breakpoint-medium) {
    text-align: left;
  }
}

.ukg-card-base-link {
  &:extend(.ukg-card-base all);
}

.ukg-image-link {
  img {
    transform: scale(1);
    transition-duration: 0.4s;
    margin: 0 auto;
  }

  &:hover {
    img {
      transform: scale(1.05);
      transition-duration: 0.5s;
    }
  }
}

.ukg-card-quote, .ukg-banner-quote, .ukg-banner-quote-large {
  > .uk-card {
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    padding: @global-gutter;
    border-radius: 12px;

    .ukg-card-quote-icon {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-evenly;

      img {
        max-width: 80px;
      }

      &:extend(.quotes all);
    }

    .uk-card-body {
      padding-top: @padding-small-padding;
      padding-bottom: 0;
    }

    @media (min-width: @breakpoint-xsmall) {
      .ukg-card-quote-icon {
        img {
          max-width: 120px;
        }
      }
    }
  }
  &.ukg-card-muted{
    > .uk-card {
      .ukg-card-quote-icon {
        &:before, &:after {
          color: @black;
        }
      }
      blockquote > p {
        &:before, &:after {
          color: @black;
        }
      }
    }
  }
}

.ukg-card-quote {
  > .uk-card {
    @media (min-width: @breakpoint-medium) {
      max-width: @quote-card-max-width;
      margin: 0 auto;
      blockquote > p {
        &:before, &:after {
          content: '';
        }
      }
    }
  }
}

.ukg-banner-quote {
  > .uk-card {
    @media (min-width: @breakpoint-medium) {
      max-width: @quote-banner-max-width;
      .ukg-card-quote-icon {
        &:before, &:after {
          content: '';
        }
      }

      .uk-card-body {
        p {
          padding: 0 @quote-padding;
        }
      }
    }
  }
}

.ukg-banner-quote-large {
  > .uk-card {
    max-width: none;

    .ukg-card-quote-icon {
      &:before, &:after {
        color: @ultikro-light-teal;
      }
    }

    blockquote > p {
      &:before, &:after {
        content: '';
      }
    }

    @media (min-width: @breakpoint-medium) {
      .uk-card-body {
        max-width: @quote-banner-max-width;
        margin: 0 auto;

        p {
          padding: 0 @quote-padding;
        }
      }
    }
  }
}

.uk-section-primary .ukg-card-hero {
  &.ukg-card-transparent {
    > .ukg-card-text {
      color: @card-transparent-color !important;

      .uk-card-title, .uk-card-body {
        color: @card-transparent-color !important;
      }
      .uk-card-preheader, .uk-card-footer-date {
        color: @ultikro-light-teal;
      }
    }
  }
}

.ukg-card-hero {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  max-width: @hero-card-max-width-mobile;

  .ukg-card-text {
    margin-bottom: @global-small-gutter;
    justify-content: center;

    > .uk-card {
      .uk-card-title {
        &:extend(h1);
      }

      .uk-card-body {
        margin-bottom: @global-small-gutter;
      }

      .uk-card-footer {
        padding: 0;
      }
    }
  }

  @media (min-width: @breakpoint-medium) {
    flex-direction: row;
    text-align: left;
    max-width: none;
    .ukg-card-text {
      margin-bottom: 0;
      justify-content: start;

      .uk-card-body {
        padding: @global-small-gutter @global-small-gutter @global-small-gutter 0;
      }
    }
  }

  @media (min-width: @breakpoint-large) {
    flex-direction: row;
    text-align: left;
    max-width: none;
    .ukg-card-text {
      margin-bottom: 0;

      .uk-card-body {
        padding-right: @global-large-gutter;
      }
    }
  }
}

.ukg-card-hero-image {
  .ukg-card-text {
    h1 {
      margin-bottom: 0;
    }
  }
}

.ukg-card-icon-icon {
  &.uk-border-circle {
    padding: @global-small-gutter;
  }
}

.ukg-card-icon {
  justify-content: center;

  > .uk-card {
    text-align: center;

    .uk-card-header {
      margin-bottom: @global-small-gutter;
    }

    .uk-card-footer {
      display: inline-flex;
      margin: @global-small-gutter auto 0;
      flex-direction: column;
      justify-content: center;

      a + a {
        margin-top: @global-small-gutter;
      }
    }

    @media (min-width: @breakpoint-small) {
      max-width: @uk-card-max-width;
      .uk-card-footer {
        flex-direction: row;
        display: flex;

        a + a {
          margin-top: 0;
          margin-left: @global-small-gutter;
        }
      }
    }
    @media (min-width: @breakpoint-medium) {
      .uk-card-header {
        margin-bottom: @global-gutter;
        padding: 0;
      }

      .uk-card-body {
        padding: 0;
      }

      .uk-card-footer {
        margin-top: @global-gutter;
        padding: 0;
        flex-direction: row;
        justify-content: space-evenly;
        flex-wrap: nowrap;
      }
    }
  }
}


.ukg-card-image > .uk-card, .ukg-card-customer > .uk-card {
  max-width: @uk-card-max-width;
  margin: 0 auto;

  .uk-card-media-top {
    img {
      border-radius: @border-radius @border-radius 0 0;
    }
  }

  .uk-card-body, .uk-card-footer {
    padding: @global-gutter;
    max-width: 400px;
    margin: 0 auto;
  }

  .uk-card-footer {
    display: flex;
    justify-content: space-evenly;
  }

  .uk-card-title {
    font-weight: 600;
  }
}

.ukg-card-image > .uk-card {
  padding: 0;
}

.ukg-card-customer > .uk-card {
  text-align: center;
  padding: 0 0 @global-gutter 0;

  .uk-card-media-top {
    background-color: @white;
    padding: @global-small-gutter;
    border-radius: @border-radius @border-radius 0 0;

    img {
      height: 120px;
      width: auto;
    }
  }

  .uk-card-footer {
    padding: 0;
  }
}

.ukg-card-image, .ukg-card-customer {
  &.ukg-card-primary > .uk-card {


  }

  &.ukg-card-default > .uk-card {
    background-color: @white;

    .uk-card-body {
      color: @ultikro-dark-teal !important;

      .uk-card-title {
        color: @ultikro-dark-teal !important;
      }
    }
  }
}

.uk-section-primary .ukg-card-customer {
  &.ukg-card-default > .uk-card {
    .uk-card-body {
      border-top: 2px solid @ultikro-light-grey;
    }
  }
}

.ukg-card-bio > .uk-card {
  padding-top: @global-gutter;
  max-width: @uk-card-max-width;
  margin: 0 auto;

  .uk-card-header {
    .uk-grid-margin {
      margin-top: @global-small-gutter;
    }

    img {
      max-width: 120px;
    }

    .uk-card-title {
      font-weight: 600;
    }
  }

  .uk-card-body {
    padding: 0 @global-gutter @global-gutter @global-gutter;
    text-align: left;
  }

  @media (min-width: @breakpoint-small) {
    .uk-card-header {
      padding: 0 @global-gutter;

      .uk-card-title {
        text-align: left;
      }

      .uk-card-body {
        padding-top: @global-small-gutter;
      }

    }
  }
}


.ukg-callout {
  &.ukg-callout-menu-content {
    > .uk-card {
      padding: @global-gutter;
      flex-direction: column;
      max-width: 320px;
      margin: 0 auto;

      .uk-card-media {
        &.ukg-card-media-menu {
          .gartner-logo-container {
            background: @white;
            position: relative;
            width: 100%;
            display: flex;
            align-items: center;
            height: 100px;

            .gartner-logo {
              position: absolute;
              width: 100%;
              height: auto;
              padding: @global-small-gutter;
            }
          }
        }
      }

      .ukg-callout-text {
        padding-top: @global-small-gutter;
      }

      @media (min-width: @breakpoint-small) {
        padding-left: @global-small-gutter;
        padding-right: @global-small-gutter;
        .uk-card-media {
          &.ukg-card-media-menu {
            .gartner-logo-container {
              height: auto;

              &:after {
                content: "";
                display: block;
                padding-bottom: 100%;
              }
            }
          }
        }
      }
      @media (min-width: @breakpoint-large) {
        flex-direction: row;
        max-width: none;
        padding-top: 0;
        .uk-card-media {
          padding-top: @global-small-gutter;
        }
      }
    }
  }

  &.ukg-callout-icon {
    > .uk-card {
      padding: @global-gutter;
      max-width: @uk-banner-max-width;
      margin: 0 auto;

      .uk-cover-container {
        padding: @global-small-gutter;
        margin-bottom: @global-gutter;

        img {
          max-width: 120px;
        }
      }

      .uk-card-body {
        margin: @global-small-gutter 0;
      }

      @media (min-width: @breakpoint-small) {
        padding: @global-small-gutter;
        .uk-cover-container {
          margin: 0;
          padding-left: 0;
          padding-right: 0;

          img {
            max-width: 200px;
          }
        }

        .ukg-callout-text {
          text-align: left;
          display: flex;
          flex-direction: column;
          justify-content: space-evenly;
          padding: @global-small-gutter;

        }
      }
      @media (min-width: @breakpoint-large) {
        .ukg-callout-text {
          .uk-card-body, .uk-card-header, .uk-card-footer {
            padding: 0;
          }
        }

        .uk-cover-container {
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }

  &.ukg-callout-image, &.ukg-callout-carousel, &.ukg-callout-video {
    > .uk-card {
      padding: 0;
      max-width: @uk-banner-max-width;
      margin: 0 auto;
      // Targeting the container that holds to image to radius it
      .uk-cover-container {
        border-radius: @border-radius @border-radius 0 0;
      }

      .ukg-callout-text {
        padding: @global-gutter;
        margin-top: 0;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;

        .uk-card-header, .uk-card-body, .uk-card-footer {
          padding: 0;
        }

        .uk-card-body {
          margin: @global-small-gutter 0;
        }
      }

      @media (min-width: @breakpoint-small) {
        .ukg-callout-text {
          text-align: left;
        }

        .uk-cover-container {
          &.uk-card-media-left {
            border-radius: @border-radius 0 0 @border-radius;
          }

          &.uk-card-media-right {
            border-radius: 0 @border-radius @border-radius 0;
          }
        }
      }
      @media (min-width: @breakpoint-medium) {
        .ukg-callout-text {
          position: relative;
          padding: @global-small-gutter;
        }
      }
      @media (min-width: @breakpoint-large) {

      }
    }

    &.ukg-callout-image-large, &.ukg-callout-carousel-large, &.ukg-callout-video-large {
      > .uk-card {
        max-width: none;
        @media (min-width: @breakpoint-medium) {
          .ukg-callout-text {
            position: relative;
            padding: @global-gutter;
            justify-content: center;

            .uk-card-header {
              .uk-card-title {
                font-size: @large-card-heading;
              }
            }

            .uk-card-body {

              p {
                font-size: @global-medium-font-size;
              }
            }

            .uk-card-footer {
              .ukg-cta {
                font-size: @cta-link-size-large;
              }
            }
          }
        }
      }
    }
  }
}

.ukg-callout-image-cover {
  &:extend(.ukg-callout-image all);
}

.ukg-card-form {
  &:extend(.ukg-card-base all);

  > .uk-card {

    max-width: @global-card-container-max-width;
    margin: 0 auto;

    .webform-required > .form-required {
      &:after {
        display: inline-block;
        margin: 0 0.3em;
        content: "";
        vertical-align: super;
        background-image: url('../files/images/background/required.svg');
        background-repeat: no-repeat;
        width: 7px;
        height: 7px;
        background-size: 7px 7px;
      }
    }

    label.visually-hidden {
      display: none;
    }

    .form-wrapper, .uk-fieldset {
      margin-top: @global-small-gutter;
    }

    .form-item {
      margin-bottom: @global-small-gutter;
      border-radius: 5px;
    }
  }

}

.ukg-callout-image-3col {
  &:extend(.ukg-callout-image all);

  > .uk-card {
    max-width: none !important;
    padding: @global-small-gutter !important;

    .uk-card-header, .uk-card-body, .uk-card-footer {
      padding: 0;
    }
  }
}

.ukg-callout-carousel {
  > .uk-card {
    padding: @global-gutter !important;

    .callout-slide {
      .ukg-card-image {
        > .uk-card {
          .uk-card-body {
            padding: @global-small-gutter;
          }

          .uk-card-footer {
            padding-top: 0;
            padding-bottom: 0;
          }
        }
      }
    }
  }
}

.ukg-callout-video {
  > .uk-card {
    .uk-cover-container {
      display: flex;
      align-items: center;
    }

    @media (min-width: @breakpoint-small) {
      text-align: center;
      .uk-cover-container {
        &.uk-card-media-left {
          border-radius: @border-radius @border-radius 0 0 !important;
        }

        &.uk-card-media-right {
          border-radius: @border-radius @border-radius 0 0 !important;
        }
      }
    }

    @media (min-width: @breakpoint-medium) {
      .ukg-callout-text {
        text-align: left;
      }

      .uk-cover-container {
        &.uk-card-media-left {
          border-radius: @border-radius 0 0 @border-radius !important;
        }

        &.uk-card-media-right {
          border-radius: 0 @border-radius @border-radius 0 !important;
        }
      }
    }
  }
}

.uk-child-width-1-2\@m > .ukg-callout-icon {
  @media (min-width: @breakpoint-medium) and (max-width: @breakpoint-medium-max) {
    > .uk-card {
      flex-direction: column;

      .uk-cover-container {
        padding: @global-small-gutter;
        width: 100%;
        text-align: center;

        img {
          max-width: 120px;
        }

        &.uk-card-media-right {
          order: 0;
        }
      }

      .ukg-callout-text {
        width: 100%;
        text-align: center;

        p {
          text-align: center;
        }
      }
    }
  }
}

.uk-section-default .ukg-callout {
  &.ukg-callout-primary > .uk-card {
    .uk-card-title {
      color: @white;
    }

    .uk-card-body {
      color: @white;
    }
  }
}

.uk-section-muted .ukg-callout {
  &.ukg-callout-default > .uk-card {

    .uk-card-title {
      color: @ultikro-dark-teal;
    }

    .uk-card-body {
      color: @black;
    }
  }
}

.ukg-card-center-icon {
  > .uk-card {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0;

    img, svg {
      width: 70%;
    }
  }
}

.ukg-card-background-image {
  > .uk-card {
    border-radius: 0;
    padding: 0 !important;
  }
}

.ukg-events-callout-board {
  .ukg-events-callout {
    .ukg-callout {
      margin-bottom: @global-small-gutter;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .ukg-events-listing-board {
    .ukg-card-listing {
      height: 100%;

