}

.customer-logo {
  img {
    max-width: 120px;
  }

  text-align: center;
  @media (min-width: @breakpoint-medium) {
    img {
      max-width: 160px;
    }
  }
  @media (min-width: @breakpoint-large) {
    img {
      max-width: 180px;
    }
  }
}
