//
// Component: Slidenav
//
// Name:            Slidenav
// Description:     Component to create previous/next icon navigations
//
// Component:       `uk-slidenav`
//
// Sub-objects:     `uk-slidenav-container`
//
// Modifiers:       `uk-slidenav-previous`
//                  `uk-slidenav-next`
//                  `uk-slidenav-large`
//


// Variables
// ========================================================================

@slidenav-padding-vertical: @global-small-gutter;
@slidenav-padding-horizontal: @global-small-gutter;

@slidenav-color: @ultikro-dark-teal;
@slidenav-hover-color: @ultikro-dark-teal-40;
@slidenav-active-color: @ultikro-dark-teal-40;

@slidenav-color-primary: @white;
@slidenav-color-muted: @ultikro-dark-teal;
@slidenav-hover-color: @ultikro-light-grey;
@slidenav-active-color: @ultikro-light-grey;

@slidenav-large-padding-vertical: @global-small-gutter * 1.5;
@slidenav-large-padding-horizontal: @global-small-gutter * 1.5;

// Component
// ========================================================================

.hook-slidenav() {
  transition: color 0.1s ease-in-out;
  font-size: 0;
  border-radius: 50px;
  &:before {
    font-size: 1.5rem;
  }
}



.ukg-slider-nav-outside {
  .uk-slidenav-previous {
    margin-right: 1rem;
  }

  .uk-slidenav-next {
    margin-left: 1rem;
  }
}

.uk-slidenav-previous, .uk-slidenav-next {
  transition: color 0.1s ease-in-out;
  font-size: 0;
  border-radius: 50px;
  padding: 0.75rem;

  &:before {
    font-size: 1.5rem;
  }
  &:hover, &:focus{
    text-decoration: none;
  }
}

.uk-slidenav-previous-small {
  &:extend(.uk-slidenav-previous all);
  padding: 0.5rem;
  &:before {
    font-size: 0.8rem;
  }
}

.uk-slidenav-next-small {
  &:extend(.uk-slidenav-next all);
  padding: 0.5rem;
  &:before {
    font-size: 0.8rem;
  }
}

.ukg-slider-default {
  .uk-slidenav-next, .uk-slidenav-previous {
    background-color: @ultikro-dark-teal;

    &:before {
      color: @white;
    }
  }
}

.ukg-slider-transparent {
  .uk-slidenav-next, .uk-slidenav-previous {
    background-color: @white;

    &:before {
      color: @ultikro-dark-teal;
    }
  }
}

.ukg-slider-blue {
  .uk-slidenav-next, .uk-slidenav-previous {
    background-color: @white;

    &:before {
      color: @ultikro-dark-teal;
    }
  }
}

.ukg-slider-secondary {
  .uk-slidenav-next, .uk-slidenav-previous {
    background-color: @ultikro-dark-teal;

    &:before {
      color: @white;
    }
  }
}

.ukg-slider-primary {
  .uk-slidenav-previous, .uk-slidenav-next {
    background-color: @slidenav-color;

    &:before {
      color: @slidenav-color-primary;
    }
  }
}

.ukg-slider-muted {
  .uk-slidenav-previous, .uk-slidenav-next {
    background-color: @slidenav-color;

    &:before {
      color: @slidenav-color-primary;
    }
  }
}

