//
// Component: Dotnav
//
// ========================================================================


// Variables
// ========================================================================

@dotnav-margin-horizontal: 1rem;
@dotnav-margin-vertical: @dotnav-margin-horizontal;

@dotnav-item-width: 0.5rem;
@dotnav-item-height: @dotnav-item-width;
@dotnav-item-border-radius: 50%;

@dotnav-item-background: @white;
@dotnav-item-hover-background: @ultikro-light-grey;
@dotnav-item-onclick-background: @ultikro-light-teal-40;
@dotnav-item-active-background: @ultikro-light-teal;


// Component
// ========================================================================

.hook-dotnav() {
  justify-content: center;
  margin-top: @global-small-gutter;
  & > li {
    &:first-child {
      padding-left: 0;
    }
  }
}

.hook-dotnav-item() {

  transition: 0.2s ease-in-out;
  transition-property: background-color, border-color;

}

.hook-dotnav-item-hover() {
}

.hook-dotnav-item-onclick() {
}

.hook-dotnav-item-active() {
}


// Miscellaneous
// ========================================================================

.hook-dotnav-misc() {
}

.ukg-slider-primary {

  .uk-dotnav > * > * {
    background: @ultikro-dark-teal;
  }

  /* Hover + Focus */

  .uk-dotnav > * > :hover,
  .uk-dotnav > * > :focus {
    background-color: @ultikro-dark-teal-40;
  }

  /* OnClick */

  .uk-dotnav > * > :active {
    background-color: @ultikro-dark-teal-40;
  }

  /* Active */

  .uk-dotnav > .uk-active > * {
    background-color: @ultikro-light-teal;
  }

}
.ukg-slider-muted {

  .uk-dotnav > * > * {
    background: @ultikro-dark-teal;
  }

  /* Hover + Focus */

  .uk-dotnav > * > :hover,
  .uk-dotnav > * > :focus {
    background-color: @ultikro-dark-teal-40;
  }

  /* OnClick */

  .uk-dotnav > * > :active {
    background-color: @ultikro-dark-teal-40;
  }

  /* Active */

  .uk-dotnav > .uk-active > * {
    background-color: @ultikro-light-teal;
  }

}

.ukg-slider-secondary {

  .uk-dotnav > * > * {
    background: @ultikro-dark-teal;
  }

  /* Hover + Focus */

  .uk-dotnav > * > :hover,
  .uk-dotnav > * > :focus {
    background-color: @ultikro-dark-teal;
  }

  /* OnClick */

  .uk-dotnav > * > :active {
    background-color: @white;
  }

  /* Active */

  .uk-dotnav > .uk-active > * {
    background-color: @white;
  }

}

