/**
 * @file
 * Counter element styles.
 * Overwriting: /modules/contrib/webform/css/webform.element.counter.css
 */

.ukg-webform {
  input.webform-counter-warning,
  input.form-text.webform-counter-warning,
  textarea.webform-counter-warning,
  textarea.form-textarea.webform-counter-warning {
    border-color: @alert-danger-background;
    background: @alert-danger-background;
  }

  .text-count-wrapper.webform-counter-warning {
    color: @alert-warning-color;
    font-weight: bold;
  }

  .uk-legend {
    &:extend(h2 all);
  }

  .uk-fieldset {
    margin-bottom: @global-gutter;

    .fieldset-wrapper {
      section.webform-section {
        margin: @global-small-gutter 0;
        .ukg-form-fields-container{
          margin-top: @global-small-gutter;
        }
      }
    }
  }

  .ukg-form-fields-container {
    margin-top: @global-gutter;
  }

  .uk-input,
  .uk-select,
  .uk-textarea {
    color: @ultikro-dark-teal;
  }

  .webform-required > .form-required {
    &:after {
      display: inline-block;
      margin: 0 0.3em;
      content: "";
      vertical-align: super;
      background-image: url('../files/images/background/required.svg');
      background-repeat: no-repeat;
      width: 7px;
      height: 7px;
      background-size: 7px 7px;
    }
  }

}
