  font-weight: 600;
  text-transform: none;
  position: relative;
  margin-right: @global-gutter;
  box-sizing: border-box;
  align-items: flex-start;
}

.hook-navbar-nav-item-hover() {
}

.hook-navbar-nav-item-onclick() {
}

.hook-navbar-nav-item-active() {
