.uk-navbar-left {
  a.uk-link {
    color: @ultikro-light-teal !important;
    font-weight: 600;

    &:hover {
      border-bottom: 1px solid @ultikro-light-teal;
      text-decoration: none;
    }

    &:hover {
      color: @ultikro-light-teal;
    }
  }
}

.uk-navbar-right {
  a.uk-link {
    margin-left: @global-small-gutter;

    &:hover {
      border-bottom: 1px solid @white;
      text-decoration: none;
    }

    &:hover {
      color: @ultikro-light-teal;
    }
  }

  a.uk-navbar-toggle {
    color: @white !important;
  }
}


// UKG navbar overwrite
.uk-navbar, .ukg-navbar-container {

  li {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    min-height: 44px;

    a {
      color: @white;
    }

    &.menu-level-1:not(.rounded-menu-link) {
      &:active, &:hover, &:focus, &:visited {
        > a {
          color: @white !important;
          border-bottom: 3px solid @white;
          text-decoration: none;
        }
      }

      > a {
        box-sizing: border-box;
        position: relative;
        height: 28px;

        &[aria-expanded="false"] {
          &:after {
            font-family: 'icomoon' !important;
            font-style: normal;
            font-weight: normal;
            font-variant: normal;
            text-transform: none;
            content: "\e930";
            padding-left: 5px;
            color: @white;
            position: absolute;
            font-size: 0.5rem;
            right: 0;
            transform: translate(1rem, 2px);
          }
        }

        &.uk-open {
          border-bottom: 3px solid @ultikro-dark-teal;
          color: @white;

          &:after {
            font-family: 'icomoon' !important;
            font-style: normal;
            font-weight: normal;
            font-variant: normal;
            text-transform: none;
            content: "\e92f";
            padding-left: 5px;
            color: @white;
            position: absolute;
            font-size: 0.5rem;
            right: 0;
            transform: translate(1rem, 2px);
          }
        }
      }
    }

    &.menu-level-2 {

      > a {
        text-align: left;
      }
    }
  }

}


#ukg-nav-mobile {
  padding: 10px 0;

  .ukg-nav-mobile-container {
    width: 100%;
    flex-direction: column;

    .ukg-navbar-main {
      > ul {
        width: 100%;
        padding: @global-small-gutter;
      }

      li {
        margin: 0 auto;
        display: block;
        max-width: none;

        a {
          color: @white;
          font-size: 1rem;
          text-align: center;
          max-width: 200px;
          padding: 5px 20px !important;
          margin: 0 auto;
        }

        &.menu-level-1 {
          &.uk-parent {
            > a {
              position: relative;

              &:after {
                font-family: 'icomoon' !important;
                font-style: normal;
                font-weight: normal;
                font-variant: normal;
                text-transform: none;
                content: "\e930";
                padding-left: 5px;
                color: @white;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                font-size: 0.5rem;
              }
            }
          }

          &.uk-open {
            background-color: @ultikro-dark-teal;
            border-radius: 15px;

            > a {
              &:after {
                content: "\e92f";
                color: @ultikro-light-teal;
              }
            }
          }

        }
      }

    }
  }

  @media (min-width: @breakpoint-small) {
    .ukg-nav-mobile-container {
      padding: @global-small-gutter;
      flex-direction: row;

      .ukg-navbar-main {
        margin-right: @global-small-gutter;

        li {
          margin: 0;

          a {
            text-align: left;
            margin: 0;
          }
        }
      }
    }
  }
}

.uk-navbar-transparent, .uk-background-white {

  .uk-navbar {
    @media (max-width: @breakpoint-xsmall-max) {
      flex-direction: column-reverse;
      align-items: center;
      .uk-navbar-right {
        margin: 0 auto;
      }
    }
  }

  a.uk-link, span {
    color: @ultikro-dark-grey-60;
    height: 30px;
    font-size: 1rem;
    box-sizing: border-box;
  }

  a.uk-link {
    &:hover {
      text-decoration: none;
    }

    &:hover {
      border-bottom: 1px solid @ultikro-dark-teal;
      color: @ultikro-dark-teal;
    }
  }
}


