#ukg-nav-language {
  background-color: @navbar-dropdown-background;

  .uk-navbar-dropdown-nav {
    margin: @global-small-gutter auto;
  }

  li {
    &.rounded-menu-link {
      max-width: 300px;
    }
  }
}

.ukg-header-container {
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .ukg-logo {
    display: flex;
    align-items: center;
    padding: @global-gutter 0;
  }

  .ukg-header-right {

    .ukg-header-right-top {
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }

    .ukg-header-right-bottom {
      margin-top: @global-small-gutter;

      .ukg-navbar-main {
        margin-bottom: @global-small-gutter;

        .uk-navbar-nav {
          margin-left: auto;
        }

        .menu-level-1 {
          > a {
            font-size: @ukg-navbar-main-font-size;
            color: @ultikro-dark-teal !important;
            box-sizing: border-box;

            &:active, &:hover, &:focus, &:visited {
              border-bottom: 3px solid @ultikro-dark-teal !important;
            }

            &.uk-open {
              color: @ultikro-dark-teal !important;

              &:after {
                color: @ultikro-dark-teal !important;
                right: 0;
                font-size: @ukg-navbar-main-font-size / 2;
                transform: translate(@ukg-navbar-main-font-size, 10px);
              }
            }

            &:hover {
              color: @ultikro-dark-teal !important;
            }

            &[aria-expanded="false"] {
              &:after {
                color: @ultikro-dark-teal !important;
                right: 0;
                font-size: @ukg-navbar-main-font-size / 2;
                transform: translate(@ukg-navbar-main-font-size, 10px);
              }
            }
          }
        }

        .menu-level-2 {
          > a {
            font-size: @navbar-nav-item-font-size;
          }
        }
      }

    }
  }

  @media (min-width: @breakpoint-medium) {
    flex-direction: row-reverse;
    align-items: flex-end;
    .ukg-search {
      display: none;
    }

    .ukg-header-right {
      max-width: 800px;
    }
  }

}


.ukg-header {
  border: 1px solid @ultikro-dark-teal-40;
  border-left: none;
  border-right: none;
}

.ukg-menu-content-container {
  padding: 0 @global-small-gutter;
}

.rounded-menu-link {
  max-width: 200px;
  margin: 0 auto;

  > a {
    color: @white !important;
    text-align: center;
    font-family: @volte;
    font-weight: 600;
    padding: 5px 0 5px 20px !important;

    &:active, &:hover, &:focus {
      transition: .1s ease-in-out;
      transition-property: color, background-color, background-position, border-color, box-shadow;
      border-radius: 500px;
      color: @ultikro-light-teal !important;
      background-color: @ultikro-dark-teal;

      &:after {
        color: @ultikro-light-teal;
      }
    }
  }
}

.ukg-footer {
  .ukg-logo {
    margin-bottom: @global-small-gutter;
    max-width: 160px;

    img {
      padding: @global-small-gutter;
    }
  }

  .ukg-footer-links-group {
    flex-direction: column;
    align-items: center;
    text-align: center;

    .ukg-footer-links {

      .uk-container {
        flex-wrap: wrap;
        justify-content: center;
        margin: @global-gutter auto;
      }
    }

    .footer-link {
      color: @ultikro-dark-grey;
      font-family: @sans_pro;
      font-size: 1.1rem;
      text-align: center;
      margin: 0 20px 10px 0;
      box-sizing: border-box;
      height: 30px;

      &:hover {
        border-bottom: 1px solid ;
        text-decoration: none;

        &:after {
          color: @ultikro-dark-grey;
        }
      }

      &:after {
        content: '\00A0|\00A0';
        margin-left: 5px;
        position: absolute;
      }

      &.footer-link-main {
        font-family: @sans_pro_semibold;
      }

      &:last-child {
        &:after {
          content: '';
        }
      }
    }

    .ukg-snippet-follow {
      text-align: center;
      justify-content: center;

      a {
        font-size: 0;
        margin: 0 @global-small-gutter;

        &:hover {
          border: none;
          text-decoration: none;

          &:before {
            color: @ultikro-dark-teal-80;
          }
        }

        &:before {
          font-size: 1.2rem;
          line-height: 2rem;
          color: @ultikro-dark-teal;
        }
      }

      @media (min-width: @breakpoint-medium) {
        align-self: flex-end;
        position: relative;
        a {
          margin: 0 @global-small-gutter;
        }
      }
      @media (min-width: @breakpoint-large) {
        justify-content: flex-end;
        padding-right: 0;
        a {
          margin: 0 0 0 @global-small-gutter * 2;
        }
      }
    }
  }

  .ukg-footer-nav {
    .uk-navbar-right {
      justify-content: center;

      br {
        display: none;
      }

    }

    a.uk-link {
      color: @ultikro-dark-grey;
      font-size: 1rem;
      margin-top: 0;
    }

    .ukg-trademark {
      color: @ultikro-dark-grey;
      font-size: 1rem;
    }

    @media (min-width: @breakpoint-medium) {
      margin-top: @global-small-gutter;
    }
  }

  @media (min-width: @breakpoint-small) {
    .ukg-footer-links-group {
      .ukg-logo {
        text-align: left;

        img {
          padding: 0;
          max-width: 160px;
        }
      }

      .ukg-footer-links {

        .uk-container {
          flex-wrap: wrap;
          justify-content: flex-start;
        }
      }

    }

    .ukg-footer-nav {
      .uk-navbar {
        flex-direction: column-reverse;
        flex-wrap: nowrap;

        .uk-navbar-left, .uk-navbar-right {
          align-items: flex-start;
          margin-left: 0;
        }

        .ukg-snippet-follow {
          justify-content: flex-start;
          margin-bottom: 5px;

          a.icon {
            &:first-child {
              margin-left: 0;
            }
          }
        }
      }
    }
  }
  @media (min-width: @breakpoint-medium) {
    .ukg-footer-links-group {
      flex-direction: row;
      align-items: flex-start;

      .ukg-logo {
        margin-right: @global-gutter;
        img {
          max-width: 160px;
        }
      }

      .ukg-footer-links {
        margin-left: @global-small-gutter;
        max-width: none;

        .uk-container {
          margin: @global-gutter auto;
          &:first-child {
            margin-top: 0;
          }
        }

      }

    }

    .ukg-footer-nav {
      .uk-navbar {
        flex-direction: row;
        justify-content: space-between;

      }
    }
  }
}



