// Miscellaneous
// ========================================================================

.ukg-snippet-follow {
  text-align: center;
  justify-content: center;

  a {
    font-size: 0;
    margin: 0 @global-small-gutter;
    color: @ultikro-dark-grey;

    &:hover {
      border: none;
      text-decoration: none;

      &:before {
        color: @ultikro-dark-teal-80;
      }
    }

    &:before {
      font-size: 1.2rem;
      line-height: 2rem;
      color: @ultikro-dark-teal;
    }
  }

}

.ukg-employee-quotes {
  display: flex;
  flex-direction: column;
  margin: 0 auto;

  .ukg-employee-quote {
    display: flex;
    flex-direction: column;

    .uk-card-header {
      position: relative;
      min-height: 200px;
    }

    .uk-card-body {
      display: flex;
      align-items: center;
      text-align: center;
      min-height: 200px;
      padding: @global-gutter;
    }

    &.uk-card-secondary {
      color: @ultikro-dark-teal;
    }
  }

  @media (min-width: @breakpoint-xsmall) {
    .ukg-employee-quote {
      flex-direction: row;

      &:nth-child(2) {
        flex-direction: row-reverse;
      }
      .uk-card-body {
        min-height: 250px;
      }
    }
  }
  @media (min-width: @breakpoint-small) {
    flex-direction: column;
  }
  @media (min-width: @breakpoint-medium) {
    flex-direction: row;
    .ukg-employee-quote {

      &:nth-child(2) {
        flex-direction: row;
      }
    }
  }
  @media (min-width: @breakpoint-xxxlarge) {
    max-width: 3840px;
  }
}

#graphic-banner {
  display: none;

  .uk-card {
    max-height: 400px;
    @media (min-width: @breakpoint-large) {
      max-height: 500px;
    }
    @media (min-width: @breakpoint-xlarge) {
      max-height: 600px;
    }
    @media (min-width: @breakpoint-xxlarge) {
      max-height: none;
    }
  }
}

#ukg-hp-resource-board {
  padding: @global-medium-gutter @global-small-gutter * 1.5;
}

#ukg-press_release {
  h1 {
    margin-bottom: @global-medium-gutter;
  }

  h2 {
    margin-bottom: @global-small-margin / 2;
  }

  h3 {
    margin-top: @global-small-margin / 2;
    font-size: 1rem;
  }

  h5 {
    margin-bottom: 0;
    color: @black;
    font-weight: bold;
  }

  hr {
    margin: @global-medium-gutter auto;
  }

  .intro {
    font-size: 1.3rem;
  }

  .ukg-press-contacts {
    h2 {
      margin-bottom: @global-medium-gutter;
    }

    .ukg-contact {
      display: flex;
      flex-direction: column;
      color: @black;

      a {
        color: @black;
      }

      @media (max-width: @breakpoint-small-max) {
        &:first-child {
          margin-bottom: @global-small-gutter;
        }
      }
    }
  }
}

#contact {
  .uk-container {
    padding-top: 40px !important;
  }

  .uk-panel {
    margin-top: 0 !important;
  }
}

#new-logo {

  .uk-container {
    padding-left: 0;
    padding-right: 0;
  }

  @media (min-width: @breakpoint-small) {

    .uk-container {
      padding-top: 0;
      padding-bottom: 0;
    }

    h3 {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 20px;
      z-index: 1000;
    }
  }

  @media (min-width: @breakpoint-medium) {
    h3 {
      top: 40px;
    }
  }

  @media (min-width: @breakpoint-large) {
    h3 {
      top: 70px;
    }
  }

  h3 {
    margin-bottom: 0;
  }

  .ukg-logo-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    video {
      width: 100% !important;
      height: auto !important;
    }

    // Our video dimension is: 1200x675.
    // Ideally i need to match this size
    .logo-animation-static-container {
      width: 100%;
      height: auto;
      @media (max-width: @breakpoint-small-max) {
        opacity: 0;
      }
    }
  }
}

#ukg-searchform {
  background-color: @navbar-dropdown-background;
  padding: @global-small-gutter;
  margin: 0 auto;
}

#ukg-searchform-desktop {
  flex-grow: 1;
  max-width: 450px;
}

.search-container {
  position: relative;
  border-radius: 500px;
  border: 2px solid @ultikro-dark-teal;
  background-color: @white;
  padding: 5px 20px;
  display: flex;
  align-items: center;

  input {
    border: none;
    line-height: 1.2;
    font-size: 1.1rem;
    width: 100%;
    text-align: center;
    flex-grow: 1;

    &:focus {
      outline-width: 0;
    }
  }

  a[type='submit'] {
    color: @ultikro-dark-teal;
    font-size: 0;

    &:before {
      font-size: 1rem;
    }

    &:hover {
      text-decoration: none;
      transform: scale(1.1);
    }
  }
}

.legal {
  &:extend(.uk-text-small all);
}

.ukg-event-share {
  .ukg-snippet-share {
    &:extend(.ukg-snippet-follow all);
    text-align: center;
    justify-content: flex-start;
    align-self: inherit;
    top: 0;

    a.icon {
      margin-left: 0;
      margin-right: 0.5rem;

      > span {
        display: none;
      }

      &:before, &:after {
        color: @ultikro-dark-teal;
        font-size: @global-large-font-size;
      }
    }
  }
}

.ukg-event-type-wrapper {
  display: flex;

  img {
    width: 120px;
  }
}

