.ukg-filter-container {
  border-radius: @global-container-border-radius;
  background-color: @ultikro-dark-teal;
  padding: @global-small-gutter 0;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 0;
  margin-bottom: @global-medium-gutter;

  .ukg-dropdown-filter {
    display: inline-flex;
    flex-direction: column;
    margin-top: 0;
    padding: 0;

    .uk-button-dropdown-toggle {
      width: 100%;
      border-radius: 0;
      position: relative;


      &:hover, &:focus, &:active {
        background-color: @ultikro-dark-teal;
        border: none;

        h4, > span:before {
          color: @ultikro-light-teal;
        }
      }

      > span {
        font-size: 0;
        position: absolute;
        right: 1rem;

        &:before {
          font-size: 1rem;
        }
      }

      h4 {
        color: @white;
        margin-bottom: 0;
        margin-left: 1rem;
      }
    }

    .filter-container {
      background-color: @navbar-dropdown-background;
      color: @white;
      margin-top: 0;

      .uk-dropdown-nav {
        padding: @global-small-gutter 5px;

        > li {
          max-width: 300px;

          > a.ukg-dropdown-link {
            color: @white !important;
            font-size: @global-small-font-size;
            padding: 5px @padding-small-padding !important;
            white-space: normal;
          }

          &.uk-active {
            > a {
              transition: .1s ease-in-out;
              transition-property: color, background-color, background-position, border-color, box-shadow;
              border-radius: 500px;
              color: @ultikro-light-teal !important;
              background-color: @ultikro-dark-teal !important;
            }
          }
        }
      }
    }
  }

  @media (min-width: @breakpoint-small) {
    border-radius: 0;
    background-color: @white;
    flex-direction: row;
    padding-right: 0;
    padding-top: 0;
    > .ukg-dropdown-filter {
      padding: @global-small-gutter 0;
      border-radius: @global-container-border-radius;
      background-color: @ultikro-dark-teal;
      margin-left: @global-small-gutter;

      .filter-container {
        margin-top: @global-small-gutter;

        .uk-dropdown-nav {
          padding: @global-small-gutter @global-small-margin;

          > li.rounded-menu-link {
            margin: 0 auto;

            > a {
              font-size: 1rem;
            }

            &.uk-nav-divider {
              margin: 1rem auto 0;
              border-top: 2px solid @white;
            }
          }
        }
      }

      &:first-child {
        margin-left: 0;
      }
    }
  }
}

.ukg-filter-label-container {
  margin: @global-medium-gutter 0;

  .ukg-filter-labels {
    margin-bottom: @global-small-gutter;
  }

  .filterLabel {
    margin-right: @global-small-gutter;
    position: relative;

    &:after {
      font-family: 'icomoon' !important;
      font-style: normal;
      font-weight: normal;
      font-variant: normal;
      text-transform: none;
      line-height: 1;
      font-size: @global-small-font-size;
      margin-left: 1rem;

      /* Better Font Rendering =========== */
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;

      content: "\e933";
    }
  }
}

.uk-pager-inactive {
  display: none;
}

.ukg-view-load-more {
  visibility: hidden;
  font-size: @global-small-font-size;
}

.ukg-listing-container {
  .uk-divider-pager {
    text-align: center;
    color: @ultikro-dark-teal-80;
    font-size: @global-small-font-size;
  }

  .ukg-list-alternate {
    &:nth-child(even) {
      background-color: @ultikro-light-grey;
    }
  }
}

