//
// Component: Base
//
// ========================================================================


// Variables
// ========================================================================

//
// New
//


@base-heading-font-family: @volte;
@base-heading-font-weight: 600;
@base-h1-line-height: 1;

@base-blockquote-font-size: @global-font-size;
@base-blockquote-line-height: @global-line-height;
@base-blockquote-font-style: italic;
@base-blockquote-margin-vertical: @global-margin;
@base-blockquote-footer-margin-top: @global-small-margin;
@base-blockquote-footer-font-size: @global-medium-font-size;
@base-blockquote-footer-line-height: 1.5;
@base-margin-vertical: @global-small-gutter;
@quote-color: @ultikro-light-teal-40;
@quote-color-inverse: @ultikro-dark-teal;
@quote-color-white: @white;
@base-em-color: @ultikro-dark-teal;
@base-selection-background: @ultikro-blue;

// UltiKro Overwrite

.hook-base-body {
  * {
    font-variant-ligatures: none;
  }
}

/* Blockquotes
 ========================================================================== */

.hook-base-blockquote() {
  position: relative;
  z-index: 1;
  footer {
    font-family: @volte;
    font-style: normal;
    font-weight: 600;
  }
  p {
    &:extend(.quotes all);
    position: relative;

    &:before, &:after {
      display: none;
      position: absolute;
      top: 20%;
      transform: translateY(-50%);
      z-index: -1;
    }

    &:before {
      left: 0;
    }

    &:after {
      right: 0;
    }
  }
  @media (min-width: @breakpoint-medium) {
    p {
      &:before, &:after {
        display: block;
      }
    }
  }
}

.quotes {
  &:before, &:after {
    font-family: 'icomoon' !important;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    opacity: 0.2 !important;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 2rem;
  }

  &:before {
    content: "\e916";
  }

  &:after {
    content: "\e913";
  }

  @media (min-width: @breakpoint-xsmall) {
    &:before, &:after {
      font-size: 3rem;
    }
  }
  @media (min-width: @breakpoint-small) {
    &:before, &:after {
      font-size: 4rem;
    }
  }
  @media (min-width: @breakpoint-medium) {
    &:before, &:after {
      font-size: 5rem;
    }
  }
}

.remove-quotes {
  &:before, &:after {
    content: '';
  }
}

/* Add margin if adjacent element */
* + blockquote {
  margin-top: @base-blockquote-margin-vertical;
}

/*
 * Content
 */

blockquote p:last-of-type {
  margin-bottom: 0;
}

.ukg-card-quote, .ukg-banner-quote {
  &.uk-card-secondary {
    blockquote, p, footer {
      color: @ultikro-dark-teal !important;
    }

    p, .ukg-card-quote-icon {
      &:before, &:after {
        color: @ultikro-light-teal-40 !important;
      }
    }
  }

  &.uk-card-default {
    blockquote, p, footer {
      color: @ultikro-light-teal !important;
    }

    p, .ukg-card-quote-icon {
      &:before, &:after {
        color: @ultikro-light-teal !important;
      }
    }

  }

  &.uk-card-transparent {
    blockquote, p, footer {
      color: @ultikro-dark-teal !important;
    }

    p, .ukg-card-quote-icon {
      &:before, &:after {
        color: @ultikro-dark-teal !important;
      }
    }
  }
}





